<template>
    <div class="container">
        <div class="head">
            <div class="dh">
                <div class="fl">
                    <a class="item" href="">首页</a>
                </div>
                <div class="fr">
                    <a class="item" href="">我的订单</a>
                    <a class="item" href="">收藏夹</a>
                    <router-link class="item" to="/Login">登录</router-link>
                    <a class="item" href="">注册</a>
                </div>
            </div>
            <div class="search">
                条件
                <select v-model="selected">
                    <option v-for="item in option" :value="item.value" :key="item.value">{{ item.text }}</option>
                </select>
                <input type="text" v-model="like"> <button @click="doLike">确定</button>
            </div>
        </div>
        <div class="lbt_box">
            <div class="lbt">轮播图</div>
            <div class="lbt">轮播图</div>
            <div class="lbt">轮播图</div>
        </div>
        <div class="good_wrapper">
            <div class="good_list">
                <ul>
                    <h1>零食</h1>
                    <template v-for="object in goods">
                        <li v-if="object.cid==1" :key="object.id">
                            <div class="img"><router-link :to="{path: `/home/${object.id}`}"><img :src="doImg(object.img)" class="img"></router-link></div>
                            <div class="good_desc"><router-link :to="{path: `/home/${object.id}`}" class="link" :title="object.name">{{object.name}}</router-link></div>
                            <p class="intro">
                                <span class="qgj">抢购价：</span><span class="price">{{object.price|price}}</span>
                                <span class="sold">{{object.sold|sold}}</span>
                            </p>
                            <div class="jr_box">
                                <div class="jrgwc"><router-link :to="{}">加入购物车</router-link></div>
                                <div class="ljgm"><router-link :to="{}">立即购买</router-link></div>
                            </div>
                        </li>
                    </template>
                </ul>
                <ul>
                    <h1>书籍</h1>
                    <template v-for="object in goods">
                        <li v-if="object.cid==2" :key="object.id">
                            <div class="img"><router-link :to="{path: `/home/${object.id}`}"><img :src="doImg(object.img)" class="img"></router-link></div>
                            <div class="good_desc"><router-link :to="{path: `/home/${object.id}`}" class="link" :title="object.name">{{object.name}}</router-link></div>
                            <p class="intro">
                                <span class="qgj">抢购价：</span><span class="price">{{object.price|price}}</span>
                                <span class="sold">{{object.sold|sold}}</span>
                            </p>
                            <div class="jr_box">
                                <div class="jrgwc"><a href="">加入购物车</a></div>
                                <div class="ljgm"><a href="">立即购买</a></div>
                            </div>
                        </li>
                    </template>
                </ul>
                <ul>
                    <h1>游戏</h1>
                    <template v-for="object in goods">
                        <li v-if="object.cid==3" :key="object.id">
                            <div class="img"><router-link :to="{path: `/home/${object.id}`}"><img :src="doImg(object.img)" class="img"></router-link></div>
                            <div class="good_desc"><router-link :to="{path: `/home/${object.id}`}" class="link" :title="object.name">{{object.name}}</router-link></div>
                            <p class="intro">
                                <span class="qgj">抢购价：</span><span class="price">{{object.price|price}}</span>
                                <span class="sold">{{object.sold|sold}}</span>
                            </p>
                            <div class="jr_box">
                                <div class="jrgwc"><a href="">加入购物车</a></div>
                                <div class="ljgm"><a href="">立即购买</a></div>
                            </div>
                        </li>
                    </template>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            goods: [],
            img:[],
            search: '',
            // baseUrl: 'http://localhost:3000',
            
            // goodsDes: 'goods'
            list: [],
            /*
            */
            like: '',
            selected: 0,
            option:[
                {text: '全部', value: 0},
                {text: '零食' , value: 1},
                {text: '书籍' , value: 2},
                {text: '游戏', value: 3},
            ]
        }
    },
    filters:{
        price(data){
            return '￥'+data
        },
        sold(data){
            return '月销'+data+'笔'
        }
    },

    computed:{
        doImg(){
            return param =>{
                return 'http://172.18.44.25:8888'+param;
            }
        }
    },
    methods:{
    doLike(){
        if(this.like==''){
            if(this.selected === 0){
            this.axios.get('/goodsImg')
            .then(res => {
            this.list = res.data.list
            })
        }else{
            this.axios.get('/goodsImg?type='+this.selected)
            .then(res => {
            this.list = res.data.list
            })
        }
        }else{
            if(this.selected === 0){
            this.axios.get('/goodsImg?like='+this.like)
            .then(res => {
                this.list = res.data.list
            })
            }else{
                this.axios.get('/goodsImg?type='+this.selected+'&like='+this.like)
                .then(res => {
                this.list = res.data.list
                })
            }
        }
    }
    },
    created(){
        this.axios.get('/goodsImg')
        .then(res => {
            this.goods = res.data.list
        })
        // this.axios.get('/img')
        // .then(res =>{
        //     this.img = res.data
        // })
    }
}
</script>

<style scoped>
    *{
        list-style: none;
        text-decoration: none;
        color: #424242;
    }
    .container {
        max-width: 1080px;
        margin: 0 auto;
        /* border: 1px solid black; */
    }
    .head {
        height: 130px;
        width: 100%;
        border: 1px solid black;
    }
    .lbt_box {
        height: 320px;
        width: 100%;
        border: 1px solid black;
    }
    .lbt {
        width: 33.3%;
        height: 100%;
        float: left;
        text-align: center;
        line-height: 320px;
    }
    /* .good_list{
        height: 325px;
    } */
    .img {
        height: 156px;
        width: 168px;
        margin: 0 auto;
        
    }
    ul li {
        display: block;
        width: 230px;
        float: left;
        margin: 20px;
        
    }
    ul {
        padding: 0px;
    }
    .good_desc {
        text-overflow: -o-ellipsis-lastline;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
    }
    .price {
        font-size: 16px;
        font-weight: bold;
        color: #dd182b;
    }
    .sold {
        font-size: 14px;
        color: #dd182b;
    }
    .jr_box {
        height: 43px;
        width: 230px;
        /* border:1px solid black; */
    }
    .jrgwc {
        width: 115px;
        height: 43px;
        background-color: #DDD;
        float: left;
    }
    .ljgm {
        width: 115px;
        height: 43px;
        background-color: #dd182b;
        float: left;
    }
    .jr_box a {
        display: inline-block;
        width: 115px;
        height: 43px;
        text-align: center;
        line-height: 43px;
    }
    .jrgwc a:hover {
        background-color: black;
        color: #DDD;
    }
    .ljgm a:hover {
        background-color: yellow;
        color: #dd182b;
    }
    .qgj {
        font-size: 14px;
        color: #dd182b;
    }

    .dh {
        background-color: #E9EEF3;
        width: 100%;
        height: 25%;
    }

    .dh .item {
        display: inline-block;
        padding:5px 10px;
        /* border-radius: 10px; */
        /* background-color: #999; */
    }
    .dh .item:hover {
        color: #dd182b;
    }

    .fl {
        float: left;
    }

    .fr {
        float: right;
    }

    .search {
        width: 100%;
        margin: 0 auto;
        display: inline-block;
    }
</style>



